<template>
  <div>
    <el-row style="height: 840px;">
      <search-bar @onSearch="searchResult" ref="searchBar"></search-bar>
      <el-tooltip effect="dark" placement="right"
                  v-for="item in books.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                  :key="item.id">
        <p slot="content" style="font-size: 14px;margin-bottom: 6px;">{{item.bookname}}</p>
        <p slot="content" style="font-size: 13px;margin-bottom: 6px">
          <span>{{item.author}}</span> /
          <span>{{item.type}}</span> /
          <span>{{item.publisher}}</span>
          <span>{{item.summary}}</span>
        </p>
        <p slot="content" style="width: 300px" class="abstract">{{item.abs}}</p>
        <el-card style="width: 135px;margin-bottom: 20px;height: 233px;float: left;margin-right: 15px" class="book"
                 bodyStyle="padding:10px" shadow="hover">
          <div @click="goDetail" class="cover">
            <img :src="temcover" alt="封面">
          </div>
          <div class="info" @click="goDetail">
            <el-link :underline="false">{{item.bookname}}</el-link>
          </div>

          <div class="author">{{item.author}}</div>
        </el-card>
      </el-tooltip>
    </el-row>
    <el-row>
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="pagesize"
        :total="books.length">
      </el-pagination>
    </el-row>
  </div>
</template>

<script>
import SearchBar from './SearchBar'
import ViewSwitch from './ViewSwitch'

export default {
  name: 'Books',
  components: {SearchBar, ViewSwitch},
  data () {
    return {
      temcover:"https://zjjjjjj_----zjjj.gitee.io/javaee/image/cover.png",
      books: [{
        cover: '',
        bookname: '',
        author: '',
        type: '',
        publisher: '',
        summary: ''
      }],
      currentPage: 1,
      pagesize: 18
    }
  },
  mounted: function () {
    this.loadBooks()
  },
  methods: {
    loadBooks () {
      var _this = this
      this.$axios
        .get('/books')
        .then(successResponse => {
          if (successResponse.data.code === 200) {
            _this.books = successResponse.data.result
            this.$message.error(successResponse.data.message);
          }
          else
            this.$message.error("反应成功")
        })
        .catch(failResponse => {
          this.$message.error("数据发送失败");
        })
    },
    handleCurrentChange: function (currentPage) {
      this.currentPage = currentPage
    },
    searchResult () {
      var _this = this
      this.$axios
        .get('/search?keywords=' + _this.keyword)
        .then(successResponse => {
        if (successResponse.data.code === 200) {
          _this.books = successResponse.data.result
          this.$message.error(successResponse.data.message);
        }
      })
    },
    goDetail(){
      this.$router.replace('/bookparticulars')
    }
  }
}
</script>
<style scoped>

.cover {
  width: 115px;
  height: 172px;
  margin-bottom: 7px;
  overflow: hidden;
  cursor: pointer;
}

img {
  width: 115px;
  height: 172px;
  /*margin: 0 auto;*/
}

.title {
  font-size: 14px;
  text-align: left;
}

.author {
  color: #333;
  width: 102px;
  font-size: 13px;
  margin-bottom: 6px;
  text-align: left;
  clear:both;
}

.abstract {
  display: block;
  line-height: 17px;
}

.el-icon-delete {
  cursor: pointer;
  float: right;
}

.switch {
  display: flex;
  position: absolute;
  left: 780px;
  top: 25px;
}

a {
  text-decoration: none;
}

a:link, a:visited, a:focus {
  color: #3377aa;
}
.info{
  float:left;
}

</style>



